
.Backdrop {
    display: none;
}
@media screen and (max-width: 480px) {
    .Backdrop {
        display: block;
        height: 100vh;
        width: 100vw;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 9;
        background-color: rgba(0, 0, 0, 0.5)
    }
}
